
/* chat container */
.chat {
  height: 100%;
  display: grid;
  grid-template-rows: 1fr 50px;
  grid-template-areas:
  "chatdialog"
  "footer";
}

.chat-dialog {
  grid-area: "chatdialog";
  overflow-y: auto;
	padding-top: 24px;
}

.chat-footer {
  grid-area: "footer";
}


/* chat dialog list */
.chat-dialog-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}


/* chat dialog list container */
.chat-container-left {
  display: grid;
  grid-template-columns: minmax(auto, 460px) minmax(20px, auto);
  grid-template-areas:
  "item gap";
  justify-items: start;
}

.chat-container-right {
  display: grid;
  grid-template-columns: minmax(20px, auto) minmax(auto, 460px);
  grid-template-areas:
  "gap item";
  justify-items: end;
}

.chat-container-left .speech-bubble {
	background-color:#2682b430;
	box-shadow:2px 2px 4px #00000080;
	border-color:#00000080;
	color:#000000c0;
}

.chat-container-left .chat-item-time {
	color:#00000080;
}

.chat-container-right .speech-bubble {
	background-color: #57a1ca;
	color: #fff;
	box-shadow:2px 2px 4px #00000080;
	border-color:#00000080;
}

.chat-container-right .chat-item-time {
	color:#ffffffa0;
}


/* chat dialog list container items */
/* type: text */
.chat-item {
  grid-area: item;
  display: grid;
  grid-template-rows: auto;
  grid-template-areas:
  "text"
  "time";
  margin: 5px;
}

.chat-item-text {
  grid-area: text;
  margin: 10px 10px 0 10px;
  font-size: 13px;
}

.chat-item-time {
  grid-area: time;
  justify-self: end;
  margin: 3px 10px 5px 0px;
  font-size: 10px;
}

.speech-bubble {
  -webkit-border-radius: 4px;
  border-radius: 4px;
  line-height: 1.3;
  border: 1px solid #000;
	line-height:115%;
}

/* type: image */
.chat-image {
  grid-area: item;
  margin: 5px;
  max-width: 100%;
  height: auto;
  -webkit-border-radius: 4px;
  border-radius: 4px;
  position: relative;
}

/*  type: selection */
.chat-selection {
  grid-area: item;
  display: grid;
  grid-template-rows: auto;
  grid-template-columns: auto 30px;
  grid-template-areas:
  "text edit"
  "options options"
  ". time";
  margin: 5px;
	border: 1px solid #000;
}

.chat-selection-edit {
  all: inherit;     /* remove all button styles */
	grid-area: edit;
	height: 15px;
	margin: 2px 6px 3px 6px;
  padding-right: 5px;
	cursor: pointer;
	border: none;
	font-size: 11px;
	color: #fff;
	box-shadow: none;
	background-color:#0000;
	color:#57a1ca;
	overflow: hidden;
	width: 14px;
	font-size: 14px;
}

.chat-selection-edit:hover {
	color:#f00;
}

.chat-selection-list {
  grid-area: options;
  margin: 5px 15px 5px 20px;
  padding: 0;
  list-style-type: none;
}

.hide {
  display: none;
}


.chat-selection-button {
  all: inherit;     /* remove all button styles */
  width: 100%;
  cursor: pointer;
  outline: none;
  margin: 2px 2px 5px 2px;
  -webkit-border-radius: 4px;
  border-radius: 4px;
  font-size: 13px;
  color: #000b;
	background-color: #f2f2f2;
}

.chat-selection-button.selected {
  /* pointer-events: none; */
}

.chat-selection-button:hover {
  box-shadow: 2px 2px 4px #00000040;
	color: #000;
}

.chat-selection-button-text {
  margin: 5px;
}
