html, body{
  margin: 0px;
  padding: 0px;
  font-family: "Ubuntu", sans-serif;
}

/* chatbot container */
.chatbot {
  height: 100vh;
  width: 100vw;
  display: grid;
  grid-template-columns: minmax(220px,2fr) 3fr;
  grid-template-rows: 60px auto;
  grid-template-areas:
  "channelsheader chatsheader"
  "channels chats";
}

.channel-header-container {
  grid-area: "channelsheader";
	color: #fff;
}

.channel-header-container,
.chat-header-container {
	background-color: #080808b0;
}

.chat-header-container {
  grid-area: "chatsheader";
}

.channel-container {
  grid-area: "channels";
  overflow-y: auto;
  border-right: 1px solid #d3d3d3;
}

.chat-container {
  grid-area: "chats";
	background-color: #f2f2f2;
  overflow-y: auto;
}


/* chat header */
.chat-header {
  height: 100%;
  display: grid;
  grid-template-columns: auto max-content max-content;
  grid-template-areas:
  "title button button";
  align-items: center;
}

.chat-header-title {
  grid-area: "title";
  margin: 0px 10px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.chat-header-button-container {
  grid-area: "button";
  margin: 0px 5px 0px 0px;
}

.chat-header-button {
  all: inherit;     /* remove all button styles */
  cursor: pointer;
  font-size: 13px;
  padding: 15px;
  color: #fff;
	border:1px solid #eee;
	background-color:#888;
}

.chat-header-button:hover {
  color: #000;
	background-color:#eee;
}

.channel-header {
  margin: 20px 0px 0px 20px;
  cursor: default;
}

/* channel list */
.channel-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

/* channel list button */
.channel-list-button {
  all: inherit;     /* remove all button styles */
  width: 100%;
  cursor: pointer;
  outline: none;
}

.channel-list-button:hover {
	background-color: #f2f2f2;
}

.channel-list-button.active {
	background-color: #f4f4f4;
}


/* channel list item */
.channel-item-container {
  display: grid;
  grid-template-columns: 15px 70px auto auto;
  grid-template-rows: 35px 35px;
  grid-template-areas:
  "status image headline time"
  "status image subline  unread";
	border-bottom: 1px dotted #d3d3d3
}

.channel-item-status.active {
  grid-area: status;
  background-color: #2682b4;
	border-right:1px solid #000;
}

.channel-item-image {
  grid-area: image;
  border-radius: 50%;
  margin: 10px;
  background-color: #2682b4;
	border: 1px solid #000;
	background-image: url(../shrimp.png);
}

.channel-item-headline {
  grid-area: headline;
  margin: 0px 5px;
  align-self: end;
  font-size: 14px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.channel-item-time {
  grid-area: time;
  justify-self: end;
  margin: 5px;
  font-size: 11px;
  color: grey;
}

.channel-item-subline {
  grid-area: subline;
  margin: 3px 20px 0px 5px;
  font-size: 13px;
  color: grey;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.channel-item-unread {
  grid-area: unread;
  align-self: end;
  justify-self: end;
  margin: 0 11px 15px 0;
	padding: 3px;
	font-size: 13px;
	color: #fff0;
	background-color: #fee;
	overflow: hidden;
	width: 5px;
	height: 5px;
  -webkit-border-radius: 10px;
	border-radius: 10px;
	border:1px solid #ff874d;
}
